import { URI } from "@shared/constants/app.constants";
import { Injectable } from "@angular/core";

import { forkJoin } from "rxjs";
import { Role } from "@feature/entitlement/models/role.model";
import { RubixNetworkService } from "@shared/services/rubix-network/rubix-network.service.ts";

@Injectable({
    providedIn: "root",
})
export class RoleService {
    constructor(private _RubixNetworkService: RubixNetworkService) {}
    getModules() {
        return this._RubixNetworkService.getAll(URI.MODULE);
    }
    getRoles() {
        return this._RubixNetworkService.getAll(URI.ROLE);
    }
    createRole(model: Role) {
        return this._RubixNetworkService.post(URI.ROLE, model);
    }
    editRole(id: string, model: Role) {
        return this._RubixNetworkService.onUpdate(`${URI.ROLE}/${id}`, model);
    }
    deleteRole(id: string) {
        return this._RubixNetworkService.onDelete(`${URI.ROLE}/${id}`);
    }
    forkRolesData() {
        return forkJoin([
            this.getRoles(),
            this.getModules(),
            this.getPermissions(),
        ]);
    }
    getPermissions() {
        return this._RubixNetworkService.getAll(URI.PERMISSION);
    }
    getSelectedPermissions(data, element) {
        const checked = Object.keys(element).filter((key) => {
            return element[key] == true;
        });
        const module = data.modules.find(
            (module) => module.id === element.module.id
        );
        const permissions = [];
        checked.forEach((key) => {
            const permission = element.module.permissions.find(
                (item) => item.record_type == key
            );

            permissions.push({ id: permission.module_permission_id });
        });
        if (data.role.id && data.role.id.length > 0) {
            module.permissions.forEach((x) => {
                const exist = permissions.find(
                    (item) => item.id == x.module_permission_id
                );
                if (!exist && x.value) {
                    permissions.push({
                        id: x.module_permission_id,
                        _deleted: true,
                    });
                }
            });
        }

        return permissions;
    }
}
